import * as Yup from 'yup'

const validationSchema = Yup.object().shape({
    token: Yup.string().required('  '),
    email: Yup.string()
        .email(' email ')
        .required(' email'),
    password: Yup.string()
        .min(4, '    4 ')
        .required(' '),
    confirmPassword: Yup.string()
        .oneOf([Yup.ref('password'), null], '  ')
        .required('  ')
})

export default validationSchema
