import { useState } from "react";
import { Link, Outlet } from "react-router-dom"
import './main.scss'

const Main = () => {
    const [open, setOpen] = useState(true);

    const openSidePanel = (open) => {
        return setOpen(!open)
      }

    return (
        <>
            <div className="main">
                <div className="main__container">
                    <div>
                        <button className= {`main__button${open ? "" : " main__button_close"}`} onClick={() => {openSidePanel(open)}}></button>
                    </div>
                    <div className= {`side-panel${open ? "" : " side-panel_hidden"}`}>
                        <Link className="side-panel__link" to="/news"></Link>
                        <Link className="side-panel__link side-panel__link_profile" to="/profile"></Link>
                        <Link className="side-panel__link side-panel__link_schedule" to="/schedule"></Link>
                    </div>
                    <div className={`side-panel__page${open ? '' : ' side-panel__page_fullscreen'}`}>
                        <Outlet />
                    </div>
                </div>
            </div>
        </>
    )
}

export default Main
